import { useState } from "react";
import { Modal, Button } from "react-bootstrap";

export const Modals = (props) => {
    const { settingsModal } = props;
    const [showSettings, setShowSettings] = settingsModal;

    // Latitude
    const [latDeg, setLatDeg] = useState("0");
    const [latMin, setLatMin] = useState("0");
    const [latSec, setLatSec] = useState("0");
    const [latRad, setLatRad] = useState("N");

    // Longitude
    const [lonDeg, setLonDeg] = useState("0");
    const [lonMin, setLonMin] = useState("0");
    const [lonSec, setLonSec] = useState("0");
    const [lonRad, setLonRad] = useState("S");

    // Footer
    const [mapZoom, setMapZoom] = useState("10");
    const [mapSize, setMapSize] = useState("6");

    const handleCloseSettings = () => setShowSettings(false);
    const applySettings = () => {
        console.log(latDeg);
        setShowSettings(false);
    };

    return (
        <Modal show={showSettings} onHide={handleCloseSettings} size="lg">
            <Modal.Header>
                <Modal.Title>Gestion des coordonnées</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="row g-3 align-items-end" novalidate>
                    <div className="col-sm">
                        <label className="form-label" for="latDeg">
                            Degré
                        </label>
                        <div className="input-group">
                            <input
                                className="form-control"
                                type="number"
                                id="latDeg"
                                min="0"
                                max="60"
                                value={latDeg}
                                onChange={(e) => setLatDeg(e.target.value)}
                            />
                            <span className="input-group-text">
                                <b>°</b>
                            </span>
                        </div>
                    </div>
                    <div className="col-sm">
                        <label className="form-label" for="lat_min">
                            Minutes
                        </label>
                        <div className="input-group">
                            <input
                                className="form-control"
                                type="number"
                                id="lat_min"
                                name="lat_min"
                                value="0"
                                min="0"
                                max="60"
                            />
                            <span className="input-group-text">
                                <b>'</b>
                            </span>
                        </div>
                    </div>
                    <div className="col-sm">
                        <label className="form-label" for="lat_sec">
                            Secondes
                        </label>
                        <div className="input-group">
                            <input
                                className="form-control"
                                type="number"
                                id="lat_sec"
                                name="lat_sec"
                                value="0"
                                min="0"
                                max="60"
                            />
                            <span className="input-group-text">
                                <b>"</b>
                            </span>
                        </div>
                    </div>
                    <div className="col-sm-2">
                        <label className="form-label" for="latitude">
                            Latitude
                        </label>
                        <div
                            name="latitude"
                            className="btn-group"
                            role="group"
                            aria-label="Basic radio toggle button group"
                        >
                            <input
                                className="btn-check"
                                type="radio"
                                id="lat_radio_n"
                                name="lat_radio"
                                value="N"
                                autocomplete="off"
                                checked
                            />
                            <label
                                className="btn btn-outline-primary"
                                for="lat_radio_n"
                            >
                                Nord
                            </label>

                            <input
                                className="btn-check"
                                type="radio"
                                id="lat_radio_s"
                                name="lat_radio"
                                value="S"
                                autocomplete="off"
                            />
                            <label
                                className="btn btn-outline-primary"
                                for="lat_radio_s"
                            >
                                Sud
                            </label>
                        </div>
                    </div>
                </div>

                <hr className="my-4" />

                <div className="row g-3 align-items-end" novalidate>
                    <div className="col-sm">
                        <label className="form-label" for="lon_deg">
                            Degré
                        </label>
                        <div className="input-group">
                            <input
                                className="form-control"
                                type="number"
                                id="lon_deg"
                                name="lon_deg"
                                value="0"
                                min="0"
                                max="180"
                            />
                            <span className="input-group-text">
                                <b>°</b>
                            </span>
                        </div>
                    </div>
                    <div className="col-sm">
                        <label className="form-label" for="lon_min">
                            Minutes
                        </label>
                        <div className="input-group">
                            <input
                                className="form-control"
                                type="number"
                                id="lon_min"
                                name="lon_min"
                                value="0"
                                min="0"
                                max="60"
                            />
                            <span className="input-group-text">
                                <b>'</b>
                            </span>
                        </div>
                    </div>
                    <div className="col-sm">
                        <label className="form-label" for="lon_sec">
                            Secondes
                        </label>
                        <div className="input-group">
                            <input
                                className="form-control"
                                type="number"
                                id="lon_sec"
                                name="lon_sec"
                                value="0"
                                min="0"
                                max="60"
                            />
                            <span className="input-group-text">
                                <b>"</b>
                            </span>
                        </div>
                    </div>
                    <div className="col-sm-2">
                        <label className="form-label" for="latitude">
                            Longitude
                        </label>
                        <div
                            className="btn-group"
                            name="latitude"
                            role="group"
                            aria-label="Basic radio toggle button group"
                        >
                            <input
                                className="btn-check"
                                type="radio"
                                id="lon_radio_o"
                                name="lon_radio"
                                value="O"
                                autocomplete="off"
                                checked
                            />
                            <label
                                className="btn btn-outline-primary"
                                for="lon_radio_o"
                            >
                                Ouest
                            </label>

                            <input
                                className="btn-check"
                                type="radio"
                                id="lon_radio_e"
                                name="lon_radio"
                                value="E"
                                autocomplete="off"
                            />
                            <label
                                className="btn btn-outline-primary"
                                for="lon_radio_e"
                            >
                                Est
                            </label>
                        </div>
                    </div>
                </div>

                <hr className="my-4" />

                <div className="row">
                    <div className="col">
                        <label className="form-label" for="zoom">
                            Zoom :
                        </label>
                        <input
                            className="form-control"
                            value="10"
                            type="number"
                            id="zoom"
                        />
                    </div>
                    <div className="col">
                        <label className="form-label" for="size">
                            Taille de la carte :
                        </label>
                        <input
                            className="form-control"
                            min="2"
                            value="6"
                            type="number"
                        />
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={handleCloseSettings}>
                    Annuler
                </Button>
                <Button variant="success" onClick={applySettings}>
                    Valider
                </Button>
            </Modal.Footer>
        </Modal>
    );
};
